@import 'reset';

$dark: #242733;
$light: #fffcf9;

$size: 50vmin;
$rows: 14;
$columns: 14;

:root {
  --color-text: #{$dark};
  --color-background: #{$light};
  --color-background-puzzle: #{desaturate(darken($light, 5%), 50%)};
  --color-input-border: #{desaturate(darken($light, 10%), 50%)};
  --color-hightlight: rgb(0, 255, 108);

  @media (prefers-color-scheme: dark) {
    --color-text: #{$light};
    --color-background: #{$dark};
    --color-background-puzzle: #{lighten($dark, 10%)};
    --color-input-border: #{lighten($dark, 40%)};
  }
}

html,
body,
.page {
  height: 100%;
}

body {
  padding: 1em;
  background: var(--color-background);
  font-family: Helvetica, Ariel, sans-serif;
  color: var(--color-text);
  font-size: clamp(18px, 5vmin, 32px);
  text-align: center;
}

.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

::selection {
  background-color: var(--color-hightlight);
}

a,
button {
  color: inherit;
  text-decoration-color: var(--color-hightlight);
  text-decoration-style: solid;
  outline: none;

  &:focus {
    text-decoration-style: dotted;
  }
}

.no-wrap {
  white-space: nowrap;
}

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.intro p {
  margin-top: 0.2em;
}

.puzzle__wrapper {
  --size: clamp(200px, 50vh, 80vw);
  width: var(--size);
  height: var(--size);
  position: relative;
  margin: 0.5em auto;
  padding: 0.2em;
  background: var(--color-background-puzzle);
}

.puzzle {
  width: 100%;
  height: 100%;
  overflow: visible;
  opacity: 1;
  transition: opacity 500ms linear;

  &.js-hidden {
    opacity: 0;
  }

  path {
    --x: 0px;
    --y: 0px;
    --rotate: 0deg;
    --transition-speed: 0;
    fill: url(#puzzle-pattern);
    stroke: var(--color-background-puzzle);
    stroke-width: 0.2%;
    transform-origin: calc(100% * var(--pieceX)) calc(100% * var(--pieceY));
    transform: translate(var(--x), var(--y)) rotate(var(--rotate));
    transition: transform var(--transition-speed) ease;
  }
}

.canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: default;
}

.links {
  li {
    display: inline-block;
  }

  li + li {
    margin-left: 0.75em;
  }

  a:focus {
    color: var(--color-hightlight);
  }

  svg {
    width: 1.5em;
    height: 1.5em;
    vertical-align: bottom;
  }
}

.newsletter {
  font-size: clamp(18px, 0.75em, 20px);
  margin-top: 1.5em;
}

.newsletter__input {
  width: clamp(200px, 80vw, 15ch);
  border: 2px solid var(--color-input-border);
  outline: none;
  margin: 0.5em 0;
  background: var(--color-background);

  &:focus {
    border-color: var(--color-hightlight);
  }
}

.disclaimer {
  font-size: 0.5em;
  font-style: italic;
}

.js-avoid {
  cursor: initial;
}
